.header-slider {
  padding: 50px;
  margin: 150px 0;
  //min-width: 0;

  &__wrapper-swiper {
    .swiper-button {
      max-width: 800px;
      position: absolute;
      display: block;

      .swiper-button-prev {
        color: white;
      }
    }

    .container {

      width: 800px;
      max-width: 1000px;
      height: 500px;

      .swiper-wrapper {
        .swiper-slide {
          padding: auto;
        }
      }
    }
  }

