  .swiper-wrapper {

    .swiper-slide {

      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 15px 0;


      &__colum-50 {

        margin-left: 80px;


        .text-55 {

          font-size: 55px;
          font-weight: 700;
          font-family: $openSans;
          line-height: 1.189;
          text-align: start;
          padding-bottom: 60px;
        }

        .text-18 {
