              background: $yellow;
              border: $yellow;
            }
          }

          .button-yellow {

            //padding: 7px 0;
            width: 230px;
            font-size: 18px;
            font-weight: 500;
            line-height: 3.633;
            font-family: $openSans;
            border: 2px solid $yellow;
            background: $yellow;
            color: $white;

            &:hover {
              border: 2px solid $white;
              background-color: transparent;
            }
          }
        }

      }

    }
